import { Component, OnInit } from '@angular/core';
import { Router } from "@angular/router";
import { NgModule, CUSTOM_ELEMENTS_SCHEMA } from '@angular/core';

@Component({
  selector: 'app-enrollcourse',
  templateUrl: './enrollcourse.component.html',
  
  styleUrls: ['./enrollcourse.component.css']

})
export class EnrollcourseComponent implements OnInit {

batches:string[];
courses:string[];
name:string;

  constructor(private router: Router) { }

  ngOnInit() {
    this.batches=['UI','UX','Python','BA'];
   this.courses=['User interface developer(UI)','User Experience Developer(UX)','Python','Business Analyst(BA)'];
   this.name='kartik';
   
  }
  logout()
  {
    this.router.navigate(['./login']);
  }



  addCourses(c){
  console.log(c);
  this.courses.unshift(c);
  return false;
  }
  
  
  deleteCourses(i){
  this.courses.splice(i,1);
  }
  selectedValue: string;
  
    foods = [
      {value: 'steak-0', viewValue: 'Steak'},
      {value: 'pizza-1', viewValue: 'Pizza'},
      {value: 'tacos-2', viewValue: 'Tacos'}
    ];
 
}



